'use client'

import { FormInput } from "../ui/FormInput/FormInput"

export const RegisterForm = () => {
    return (
        <form name="registerForm" className="flex flex-col gap-[16px] items-center justify-center border-1 border-sky-300 pt-12 pb-12 pl-10 pr-10 rounded-[20px] shadow-md">
            <h2 className="font-medium text-lg m-0">Авторизация</h2>
            <FormInput type={'text'} placeholder="Фамилия" label="Фамилия"/>
            <FormInput type={'text'} placeholder="Имя" label="Имя"/>
            <FormInput type={'email'} placeholder="E-mail" label="Email"/>
            <FormInput type={'password'} placeholder="Пароль" label="Пароль"/>
            <FormInput type={'password'} placeholder="Подтвердите пароль" label="Подтвердите пароль"/>
        </form>
    )
}